.sidebarTable{
    display: grid;
    grid-template-columns: repeat(40, 1fr);
    grid-auto-flow: row;
    grid-gap: 10px;
    margin-top: 0%;
    margin-left: 10%;
    margin-right: 10%;
    z-index: 999;
    position: relative;
    bottom: 260px;
    height: 13.2rem;
    overflow-x: scroll;
    overflow-y: hidden;
    scrollbar-width: none; /* Firefox */
    -ms-overflow-style: none;  /* IE 10+ */
    transition: .3s;
    user-select: none;
}
.sidebarTable:hover{
    transition: .3s;
    transform: scale(1.01);
}
.sidebarTable::-webkit-scrollbar { /* WebKit */
    width: 0;
    height: 0;
}

